@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,700;1,400;1,500;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

h1,h2,h3,h4,h5,h6{
  font-family: "Noto Sans" !important;
}
p {
  font-family: "Noto Sans"!important;
}
li {
  font-family: "Noto Sans"!important;
}

a {
  font-family: "Noto Sans" !important;
}
body {
  background: #fff;
}

header {
  background-color: #ffffff;
  min-height: 4.5rem;
  padding: 0 10vw;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.4);
}

.main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "poppins", sans-serif;
}

.brand-logo {
  display: flex;
  align-items: center;
  font-size: 1.25rem;
  color: inherit;
  text-decoration: none;
  color: #fff;
  font-family: "poppins", cursive, sans-serif;
  font-weight: 200;
}

.brand-logo-name {
  font-size: 1rem;
}

.main-nav ul {
  display: flex;
  margin: 0;
  padding: auto;
  list-style: none;
}

.main-nav a {
  color: black;
  text-decoration: none;
  padding: 0 1rem;
}

.main-nav a:hover {
  color: #69AB1A !important
}

.toggle-button {
  display: none;
}

.toggle-button .bar {
  height: 3px;
  width: 100%;
  background-color: #69AB1A !important;
  border-radius: 2px;
}

.top-important{
  margin-top: 10% !important;
}

.activeNav{
  color: #69AB1A!important
}
.enq-btn{
    background-color: #69AB1A !important;
    border: none !important;
    width: 100px;
    height: 33px;
    margin-left: 2%;
  }

  .top-carosuel-relative {
      position: relative;
    }
  
  .top-carosuel-absolute {
      position: absolute;
