@media only screen and (max-width: 800px) {
  .toggle-button {
    position: absolute;
    right: 30px;
    top: 15px;
    flex-direction: column;
    justify-content: space-between;
    height: 25px;
    width: 30px;
    cursor: pointer;
    display: flex;
  }

  .main-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .main-nav ul {
    flex-direction: column;
    align-items: flex-start;
    display: none;
  }

  .main-nav li {
    padding: 0.6rem 0;
  }

  .main-nav a {
    padding: 0;
  }

  .main-nav ul.active {
    display: flex;
  }
