@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,700;1,400;1,500;1,700&display=swap");

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

h1,h2,h3,h4,h5,h6{
  font-family: "Noto Sans" !important;
}
p {
  font-family: "Noto Sans"!important;
}
li {
  font-family: "Noto Sans"!important;
}

a {
  font-family: "Noto Sans" !important;
}
body {
  background: #fff;
}

header {
  background-color: #ffffff;
  min-height: 4.5rem;
  padding: 0 10vw;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.4);
}

.main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "poppins", sans-serif;
}

.brand-logo {
  display: flex;
  align-items: center;
  font-size: 1.25rem;
  color: inherit;
  text-decoration: none;
  color: #fff;
  font-family: "poppins", cursive, sans-serif;
  font-weight: 200;
}

.brand-logo-name {
  font-size: 1rem;
}

.main-nav ul {
  display: flex;
  margin: 0;
  padding: auto;
  list-style: none;
}

.main-nav a {
  color: black;
  text-decoration: none;
  padding: 0 1rem;
}

.main-nav a:hover {
  color: #69AB1A !important
}

.toggle-button {
  display: none;
}

.toggle-button .bar {
  height: 3px;
  width: 100%;
  background-color: #69AB1A !important;
  border-radius: 2px;
}

.top-important{
  margin-top: 10% !important;
}

.activeNav{
  color: #69AB1A!important
}
.enq-btn{
    background-color: #69AB1A !important;
    border: none !important;
    width: 100px;
    height: 33px;
    margin-left: 2%;
  }

  .top-carosuel-relative {
      position: relative;
    }
  
  .top-carosuel-absolute {
      position: absolute;
      top: 300px;
      right: 370px;
      left: 370px;
      width: auto;
      height: 100px;
      color: white;
      font-weight: bold;
      font-weight: 1000;
      text-transform: uppercase;
      align-items: center;
    }

    

@media only screen and (max-width: 1000px) {
  header {
    padding: 0 5vw;
  }
  .next-card{
    margin-top: "5%" !important;
  }
    .top-carosuel-absolute {
      position: absolute;
      top: 300px;
      right: 370px;
      left: 370px;
      width: auto;
      height: 100px;
      color: white;
      font-weight: bold;
      font-weight: 1000;
      text-transform: uppercase;
      align-items: center;
    }
}

@media only screen and (max-width: 600px) {
.next-card {
    margin-top: "5%" !important;
  }
                             .top-important-about {
                               margin-top: 35% !important;
                             }
    .top-important {
      margin-top: 30% !important;
    }
                            .top-carosuel-absolute {
                              position: absolute;
                              top: 300px;
                              right: 370px;
                              left: 370px;
                              width: auto;
                              height: 100px;
                              color: white;
                              font-weight: bold;
                              font-weight: 1000;
                              text-transform: uppercase;
                              align-items: center;
                            }
}

@media only screen and (max-width: 800px) {
  .toggle-button {
    position: absolute;
    right: 30px;
    top: 15px;
    flex-direction: column;
    justify-content: space-between;
    height: 25px;
    width: 30px;
    cursor: pointer;
    display: flex;
  }

  .main-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .main-nav ul {
    flex-direction: column;
    align-items: flex-start;
    display: none;
  }

  .main-nav li {
    padding: 0.6rem 0;
  }

  .main-nav a {
    padding: 0;
  }

  .main-nav ul.active {
    display: flex;
  }
        .next-card {
          margin-top: "5%";
        }
                                .top-important-about {
                                  margin-top: 35% !important;
                                }
                .top-important {
                  margin-top: 50% !important;
                }
                                                                .top-carosuel-absolute {
                                                                  position: absolute;
                                                                  top: 300px;
                                                                  right: 370px;
                                                                  left: 370px;
                                                                  width: auto;
                                                                  height: 100px;
                                                                  color: white;
                                                                  font-weight: bold;
                                                                  font-weight: 1000;
                                                                  text-transform: uppercase;
                                                                  align-items: center;
                                                                }
}